$category-border-width: 3px;
$category-border: $category-border-width solid gold;

$duties-background-color: darkslateblue;

.App {
  background-color: #282c34;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  color: white;
}

.clamp-container {
  width: 100%;
}

.clamp-content {
  margin: auto;
  max-width: 600px;
}

#header-content {
  font-size: calc(10px + 2vmin);
  text-align: center;
}

#categories {
  display: flex;
}

.categories-margin {
  flex: auto;
  border-bottom: $category-border;
}

.categories-tab {
  background-color: inherit;
  color: inherit;
  border: $category-border;
  border-radius: 10px 10px 0px 0px;
  font-size: medium;
  margin: 0px -1px;
  padding: 5px 10px 3px;
}

.categories-tab-active {
  background-color: $duties-background-color;
  border-bottom: $category-border-width solid $duties-background-color;
}

#duties {
  background-color: $duties-background-color;
  flex: auto;
  overflow: auto;

  border-bottom: $category-border;
}

.duty {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

#footer-content {
  display: flex;
  justify-content: space-between;
}
